      //
      this.searchWord = e.target.value;

      //
      var _this = this; // forEachthisthis
      let reg1 = new RegExp("<mark>",'g');
      let reg2 = new RegExp("</mark>",'g');
      this.packs.forEach(function(pack){
        pack.content = pack.content.replace(reg1, "");
        pack.content = pack.content.replace(reg2, "");
      })
      if (e.target.value !== "") {
        this.packs.forEach(function(pack){
          let reg3 = new RegExp(e.target.value,'g');
          pack.content = pack.content.replace(reg3, "<mark>" + e.target.value + "</mark>");
        })
      }
    },

