  .tab-wrap {
    background: White;
    box-shadow: 0 0 5px rgba(0,0,0,.1);
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    padding: 0 0 20px;
  }
  .tab-label {
    color: Gray;
    cursor: pointer;
    flex: 1;
    font-weight: bold;
    order: -1;
    padding: 12px 6px;
    position: relative;
    text-align: center;
    transition: cubic-bezier(0.4, 0, 0.2, 1) .2s;
    user-select: none;
    white-space: nowrap;
    -webkit-tap-highlight-color: transparent;
  }
  .tab-label:hover {
    background: rgba(0, 191, 255,.1);
  }
  .tab-switch:checked + .tab-label {
    color: DeepSkyBlue;
  }
  .tab-label::after {
    background: DeepSkyBlue;
    bottom: 0;
    content: '';
    display: block;
    height: 3px;
    left: 0;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    transform: translateX(100%);
    transition: cubic-bezier(0.4, 0, 0.2, 1) .2s 80ms;
    width: 100%;
    z-index: 1;
  }
  .tab-switch:checked ~ .tab-label::after {
    transform: translateX(-100%);
  }
  .tab-switch:checked + .tab-label::after {
    opacity: 1;
    transform: translateX(0);
  }
  .tab-content {
    height:0;
    opacity:0;
    padding: 0 0px;
    pointer-events:none;
    transform: translateX(-30%);
    transition: transform .3s 80ms, opacity .3s 80ms;
    width: 100%;
  }
  .tab-switch:checked ~ .tab-content {
    transform: translateX(30%);
  }
  .tab-switch:checked + .tab-label + .tab-content {
    height: auto;
    opacity: 1;
    order: 1;
    pointer-events:auto;
    transform: translateX(0);
  }
  .tab-wrap::after {
    content: '';
    height: 20px;
    order: -1;
    width: 100%;
  }
  .tab-switch {
    display: none;
  }
